import React,{useState} from "react";//User  

class State_User extends React.Component{
    constructor(props){
        super(props);
        this.id="User   id";
        this.state = {id : "User  this.state  id"}
    }

  /*  componentDidMount(){ // . render .  render   
        this.id = "this.id .";
        this.setState({id : 'this.state.id '});
    }
   */ 

    
  change(){ //render  .  .    
    //this.props.id = 2000;
  //this.id = "this.id .";
  //1 . this.id  state     
  //    state  this.id  re-render
  this.setState({id : 'this.state.id '});//state  re-render
  };

    render(){


        return(<div>
           <h3 style={{color:"red"}}>this.props.id= {this.props.id}  </h3>
           <h3 style={{color:"green"}}>this.id= {this.id}     </h3>
           <h3 style={{color:"blue"}}>this.state.id= {this.state.id}     </h3>
            <button onClick= {this.change.bind(this)}>{" "}</button> 
        </div>);
    }
}

function State_User2 (props){
    const [id, setId] = useState("useState "); //=useState("")
    // [id = "useState " , setId(i) { id = i;} ]

    let change = function(){ //render  .  .    
        // props.id =2000
        setId('useState ');//state  re-render
    };

    return  (<div>
           <h3 style={{color:"red"}}>props.id= {props.id}  </h3>
           <h3 style={{color:"blue"}}>useState.id= {id}     </h3>
           <button onClick= {change}>{" "}</button> 
        </div>);
}
export {State_User as default, State_User2}; 