import React from "react";
import Notification from "../Notification";

const reservedNotifications = [
    { id: 1,   message: ",   .",   },
    { id: 2,   message: " .",  },
    { id: 3,   message: "   .", },
];

var timer;

class MyNotificationList extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            notifications: [],
        };
    }

    componentDidMount() {
        const { notifications } = this.state;
        timer = setInterval(() => {
            if (notifications.length < reservedNotifications.length) { // 1 1  
                const index = notifications.length;
                notifications.push(reservedNotifications[index]);
                this.setState({
                    notifications: notifications,
                });
            } else { //3   .
                this.setState({
                    notifications: [],
                });
                //clearInterval(timer);
            }
        }, 1000);
    }

    componentWillUnmount() {//        . 3  .
        if (timer) {
            clearInterval(timer);
        }
    }

    render() {
        return (
            <div>
                {this.state.notifications.map((notification) => {
                    return (
                        <Notification
                            key={notification.id}
                            id={notification.id}
                            message={notification.message}
                        />
                    );
                })}
            </div>
        );
    }
}

export default MyNotificationList; 
