import React , {useState}from "react"; 
import { useContext } from "react";
//import ThemeContext from "./chapter_14/ThemeContext";
class BoardWriteForm extends React.Component{ 
    constructor(props){
        super(props);
        this.state={
            board : props.contents, 
            seq :  props.contents.length+1,//  
            title : '', //'',
            writer : '', //'',
            view : 0 //
            
        };
        
    }//constructor end
    render(){
        const clickHandler=(event)=>{
            //this.setState({seq : this.state.seq+1 , view : this.state.view+1});//( +1)
           // this.setState({view : this.state.view+1});//   
            //this.setState(
            // ()=>{
            //     this.state.board.push(
            //         {seq:this.state.seq , title:this.state.title, 
            //            writer : this.state.writer, view:this.state.view}) }  );
            
            alert(this.state.seq +":" + this.state.title
            + ":" + this.state.writer +":" +this.state.view);
            //alert(this.state.board.length);
         
            this.props.addBoard( {seq:this.state.seq , title:this.state.title, 
                writer : this.state.writer, view:this.state.view} );
         
             }
        const changeHandler=(event)=>{
                this.setState({[event.target.id] : event.target.value});
            }
        return (
           <div>
                
            :<input type="text" id="title" onChange={changeHandler} />
            :<input type="text" id="writer" onChange={changeHandler} />
            <button  onClick={clickHandler} ></button>
          </div>
        );
    }//render end
}//class end
class BoardHeading extends React.Component{
    render(){
        return(<thead><tr><th></th><th></th><th></th><th colSpan="2"></th></tr></thead>);
    }
}
class Board extends React.Component{

    constructor(props){
        super(props);
        //alert("Board")

        this.state = {isUpdateClick : false , updateSeq:0, updateTitle : '', updateWriter:''};
    }
    render(){
        //alert("Board");
        //alert(this.constructor == Board);

        const deleteItem = (e)=>{
           // alert("");
           // alert(this.constructor == Board);
            // this        Board. 
            // this     .
            // e.target  
            this.props.deleteBoard(e.target.id);//string 
        }

        //    id , isUpateClick true 
         const updateItem = (e)=>{
            alert("");

            this.setState({isUpdateClick : true, updateSeq:e.target.id});
         }

         //,   
        const updateHandler = (e)=>{
            //alert("");
            this.setState({[e.target.name] :e.target.value });
         }
         //       isUpateClick  false 
         const updateEndItem = (e)=>{
            const updateboard = {seq:this.state.updateSeq, title:this.state.updateTitle, writer:this.state.updateWriter} 
            console.log(updateboard);
            this.props.updateBoard(updateboard );
            this.setState({isUpdateClick : false});
         }

       const board_tr = this.props.contents.map( (oneboard, index)=>{ 
                        return (<tr key={oneboard.seq}>
                            <td>{oneboard.seq}</td>
                            <td>{oneboard.title}</td>
                        <td>{oneboard.writer}</td>
{/* 
                        {
                        this.state.isUpdateClick && this.state.updateSeq == oneboard.seq 
                        ? 
                        <td><input name='updateTitle' onChange={updateHandler} />
                        <input name='updateWriter' onChange={updateHandler} />
                        <button id={oneboard.seq} onClick={updateEndItem} className="btn btn-danger"></button>
                        </td>
                        :
                        <td><button id={oneboard.seq} onClick={deleteItem} className="btn btn-danger"></button>
                        <button id={oneboard.seq} onClick={updateItem} className="btn btn-danger"></button></td>
                        } */}
                        </tr>)
      }
      );//map end
       //   
       return(<tbody>{board_tr}</tbody>);
    }
}

