  //test2 arguments 
  constructor(){
    super();
    this.state = {toggle:true , result:""};
  }

  onClickToggle (name, id, e){// arguments    event 
    this.setState({
        result :  id + "  " + name + ":" +e.type + " ",
        toggle : !this.state.toggle
    });    
  }

  render(){    
    return <div>
    <button onClick={(e)=>{this.onClickToggle("", 100, e)}}>{this.state.toggle === true?"":""}</button>
    <h3>{this.state.result}</h3>
    </div>}  
