import React from "react";
import styled, {css} from "styled-components";

//test1  
const StyledButton = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;
  color: gray;
  background-color: white;
`;
function Button({ children }) {
  return <StyledButton>{children}</StyledButton>;
} 
 
  //test2  1
const StyledButton2 = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;

  color: ${(props) => props.color || "gray"};
  background-color: ${(props) => props.background || "white"};
`;

function Button2({ children, color, background }) {
   return (
      <StyledButton2 color={color} background={background} >
        {children}
      </StyledButton2>
    );
  }
  //test3  2
  const StyledButton3 = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;

  
  //props primary    &&  css ( )
  //css import  !!!
  ${(props) =>
    props.primary &&
    css`
    color: white;
    background-color: navy;
    border-color: navy;
    `}
    `;

function Button3({ children, ...props }) {
  return <StyledButton3 {...props}>{children}</StyledButton3>;
}

export default Button;
export {Button2, Button3};
