 class MyEventPractice2 extends React.Component{
      constructor(props){
        super(props);
        this.state = {toggle:true, result : ""}
      }


      render(){
        let onClickToggle = function(name, id, e){
          this.setState({
            result : name + ":" + id + "  " + e.type + "  " , 
                          toggle : !this.state.toggle
                        });   
        }
        return <div>
        <button onClick={onClickToggle.bind(this,"", 1)}>{this.state.toggle === true?"":""}</button>
        <h3>{this.state.result}</h3>
        </div>
      }
 }
    export {MyEventPractice as default, MyEventPractice2};


